.tableContainer{letter-spacing:0;display:flex;align-items:flex-start;flex-direction:column;border:1px solid #656565;overflow:auto;height:100%}.table{font-size:0.8em;width:100%;table-layout:fixed;border-collapse:collapse}.table tr{height:3em}.table tr th:first-child{text-align:left;width:40%;padding-left:2%}.tableHead{border-bottom:1px solid #d3d3d3}.tableBody tr:nth-of-type(even){background:#fff}.category{cursor:pointer;font-size:1.1em}.icon{opacity:0;transition:0.66s}.iconActivated{opacity:1;composes:icon}.activated{color:blue;composes:category}



